<template>
  <b-container>
    <Loader class="loader" v-if="isLoading" />

    <h2 class="post-header">
      {{ posts.length == 0 ? "No Posts Available" : "All Posts" }}
    </h2>
    <ul>
      <li
        class="post"
        @click="show(post.id)"
        v-for="post in posts"
        :key="post.id"
      >
        <h4>{{ post.title }}</h4>
        <p>{{ post.post }}</p>

        <span>
          <li @click="delayDel">
            <b-icon icon="trash-fill" aria-hidden="true"></b-icon> Delete
          </li>
          <li @click="modalShow = !modalShow">
            <b-icon icon="pencil-square" aria-hidden="true"></b-icon> Edit
          </li>
        </span>
      </li>
      <b-modal v-model="modalShow">
        <EditForm :editPost="editPost" :postId="postId" />
      </b-modal>
    </ul>
  </b-container>
</template>

<script>
import EditForm from "./EditForm";
import Loader from "./Loader";

let url = "https://cors-anywhere.herokuapp.com/http://54.144.250.94/";

export default {
  methods: {
    show(dt) {
      this.postId = `${dt}`;
    },
    async editPost(id, title, post) {
      this.modalShow = false;
      this.isLoading = true;

      try {
        let dt = await fetch(url, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            id,
            title,
            post,
          }),
        });
        // let data = await dt.json();
        this.isLoading = false;
        this.fetch();
      } catch (err) {
        alert("error occurred, please retry");
        this.isLoading = false;
      }
    },
    async deletePost() {
      this.modalShow = false;
      this.isLoading = true;
      setTimeout(() => {
      }, 100);
      try {
        let dt = await fetch(url + this.postId, {
          method: "DELETE",
        });
        // let data = await dt.json();
        this.isLoading = false;
        this.fetch();
      } catch (err) {
        alert("error occurred, please retry");
        this.isLoading = false;
      }
    },

    delayDel() {
      setTimeout(() => {
        this.deletePost();
      }, 100);
    },
  },
  data() {
    return {
      modalShow: false,
      postId: "",
      isLoading: false,
    };
  },
  props: {
    posts: Array,
    fetch: Function,
  },
  components: {
    Loader,
    EditForm,
  },
};
</script>

<style scoped>
.post-header {
  padding: 10px 0;
}

ul {
  list-style-type: none;
  text-align: left;
  padding: 10px;
}
.post {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}

.post h4 {
  border-bottom: 1px solid #ccc;
  padding: 5px;
  font-weight: 370;
}

.post p {
  padding: 5px 10px;
}
.post span {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.post span li {
  margin: 0 0 0 10px;
  cursor: pointer;
  padding: 5px;
  border: 1px solid;
  border-radius: 5px;
  font-weight: bold;
}
.post span li:nth-child(1):hover {
  background: rgb(231, 38, 38);
  color: white;
}
.post span li:nth-child(2):hover {
  background: rgb(118, 231, 38);
  color: white;
}

.post span li:nth-child(1) {
  color: red;
}
.post span li:nth-child(2) {
  color: rgb(134, 255, 47);
}
</style>
