<template>
  <div>
    <Loader class="loader" v-if="isLoading" />
    <b-button class="bt" @click="modalShow = !modalShow">
      <b-icon icon="plus"></b-icon>
      Add Post</b-button
    >

    <b-modal v-model="modalShow">
      <AddForm :publishPost="publishPost" />
    </b-modal>
  </div>
</template>

<script>
import AddForm from "./AddForm";
import Loader from "./Loader";

let url = "https://cors-anywhere.herokuapp.com/http://54.144.250.94/";

export default {
  props: {
    fetch: Function,
  },
  methods: {
    async publishPost(title, post) {
      this.modalShow = false
      this.isLoading = true;

      try {
        let dt = await fetch(url, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            title,
            post,
          }),
        });
        let data = await dt.json();
        this.isLoading = false;
        this.fetch();
      } catch (err) {
        alert("error occurred, please retry");
        this.isLoading = false;
      }
    },
  },
  components: {
    AddForm,
    Loader,
  },
  data() {
    return {
      isLoading: false,
      modalShow: false,
    };
  },
};
</script>

<style scoped>
.bt {
  background: green;
}

</style>
