<template>
  <div>
    <b-form @submit="onSubmit" @reset="onReset">
            <h4 class="text-center">Add Post</h4>

      <b-form-group>
        <b-form-input
          class="input"
          id="input-1"
          v-model="form.title"
          type="text"
          required
          placeholder="Enter post title"
        ></b-form-input>

        <b-form-textarea
          class="input"
          id="textarea"
          v-model="form.post"
          placeholder="Enter something..."
          rows="4"
          max-rows="6"
        ></b-form-textarea>
      </b-form-group>

      <b-button class="button" type="submit" variant="primary">Submit</b-button>
      <b-button class="button" type="reset" variant="danger">Reset</b-button>
    </b-form>
    <!-- <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card> -->
  </div>
</template>

<script>
export default {
  props: {
    publishPost: Function
  },

  data() {
    return {
      form: {
        title: "",
        post: "",
      },
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.publishPost(this.form.title, this.form.post);
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.title = "";
      this.form.post = "";
      this.$nextTick(() => {
        this.show = true;
      });
    },
 
  },
};
</script>

<style scoped>
.input {
  margin-bottom: 10px;
}

.button {
  margin-right: 10px;
}
</style>
