<template>
  <div id="app">
    <Nav :fetch="fetchPosts"/>
    <Loader v-if="isLoading" />
    <Posts :posts='posts' :fetch="fetchPosts"/>



<Alert v-if="isFailed"/>
  </div>
</template>
<script>
import Nav from "./components/Nav";
import Loader from "./components/Loader";
import Alert from "./components/Alert";
import Posts from "./components/Posts";

let url = "https://cors-anywhere.herokuapp.com/http://54.144.250.94/";
export default {
  watch: {},
  props: {},
  created() {
    this.fetchPosts();
  },
  components: {
    Nav,
    Loader,
    Alert,
    Posts
  },
  data() {
    return {
      posts: [],
      isLoading: false,
      isFailed: false
    };
  },
  methods: {
    async fetchPosts() {
      this.isLoading = true;
      try {
        let dt = await fetch(url);
        let posts = await dt.json();
        this.posts = [...posts];
        this.isLoading = false;
      } catch (err) {
        console.log(err);
        this.isLoading = false;
        this.isFailed =true
        this.dismiss()
      }
    },

    dismiss(){
    setTimeout(() => {
  this.isFailed = false;
}, 6000)
    }
  },
};
</script>

